
import EncounterMixinVue from "@/views/EncounterMixin.vue";
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent } from "vue";
import { Field, Option } from "@/components/Forms/FieldInterface";
import { IonPage } from "@ionic/vue";
import { HtsSocialHistoryService } from "../../services/hts_social_history";

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as HtsSocialHistoryService,
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsSocialHistoryService(
            this.patientID,
            this.providerID
          );
          this.fields = [this.maritalStatusField()];
        }
      },
      immediate: true,
    },
  },
  methods: {
    async onFinish(_: any, computedData: any) {
      await this.service.createEncounter();
      await this.service.saveObservationList(
        (await this.resolveObs(computedData)) as any
      );
      this.nextTask();
    },
    maritalStatusField(): Field {
      return {
        id: "hts_marital_status",
        helpText: "Marital status",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded("Marital status", v.value),
          };
        },
        options: () => {
          return this.toConditionalOptions([
            ["Single"],
            ["Married"],
            ["Seperated"],
            ["Divorced"],
            ["Widowed"],
          ]);
        },
      };
    },
  },
});
